
.listTweets{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    grid-gap: 20px;
}

.btnBack{
    cursor: pointer;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    text-transform: uppercase;
    margin-left: 10px;
    margin-top: 10px;
    width: 100px;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: #EBD8FF;
    color: #373737;
    box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
    border-radius: 10.3108px;
}

.btnBack:hover{
    color: white;
    background-color: tomato;
}
.btnLoadMore{
    display: block;
    cursor: pointer;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    text-transform: uppercase;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 10px;
    width: 196px;
    padding-top: 14px;
    padding-bottom: 14px;
    background-color: #EBD8FF;
    color: #373737;
    box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
    border-radius: 10.3108px;
}

.btnLoadMore:hover{
    color: white;
    background-color: tomato;
}