.wrapperFilter{
    text-align: center;
    padding-right: 20px;
    padding-top: 10px;
}

.btnChoose{
    cursor: pointer;
    font-weight: 600;
    font-size: 12px;
    line-height: 20px;
    text-align: center;
    text-transform: uppercase;
    width: 100px;
    color: #373737;
    background-color: #EBD8FF;
    box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
    border-radius: 10.3108px;
}

.btnChoose:not(:last-child){
    margin-right: 5px;
}

.btnChoose.isSelected {
    color: white;
    background-color: tomato;
}

.btnChoose:hover{
    color: white;
    background-color: tomato;
}

.btnChoose:active{
    color: white;
    background-color: rgb(180, 64, 43);
}